import React, { Component } from 'react';

import './pharmacy_dashboard.scss';
import axios from 'axios';

export default class pharmacy_dashboard extends Component {
	state = {
		pharmas: []
	};

	componentWillMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/get_all_pharmacy')
			.then((response) => {
				console.log(response);
				this.setState({ pharmas: response.data.pharmas });
			})
			.catch((err) => {
				console.log(err);
			});
	}

	render() {
		const pharmas = this.state.pharmas.reverse();

		return (
			<div className="pharmacy_dashboard">
				<h2 class="bg-primary text-center text-white p-3">Medicento Pharmacy List</h2>
				<section class="container">
					<table className="table table-bordered">
						<thead>
							<tr>
								<th>Pharmacy Name</th>
								<th>State</th>
								<th>City</th>
								<th>Edit</th>
								<th>View Details</th>
							</tr>
						</thead>
						<tbody>
							{pharmas.map((pharma) => (
								<tr>
									<td>{pharma.pharma_name}</td>
									<td>{pharma.area != null ? pharma.area.area_state : '-'} </td>
									<td>{pharma.area != null ? pharma.area.area_city : '-'}</td>
									<td>
										<span class="fa fa-pencil-alt" />
									</td>
									<td>
										<span class="fa fa-address-card" />
									</td>
								</tr>
							))}
						</tbody>
					</table>
				</section>
			</div>
		);
	}
}
